import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup
	},
	data() {
		return {
			title: '修改产品',
			reqData: {
				name: '',
				type: '',
				masterUnit: '',
				slaveUnit: '',
				unit: '',
				multiple: '',
				remarks: '',
			},
			disableSubmit: true
		}
	},
	onShow() {
		let pages =  getCurrentPages()
		let curPage = pages[pages.length - 1]
		if (curPage.data.rName && curPage.data.datas) {
			if (curPage.data.rName == 'type') {
				this.reqData.type = curPage.data.datas.name
			} else if(curPage.data.rName == 'masterUnit') {
				this.reqData.masterUnit = curPage.data.datas.name
			} else if (curPage.data.rName == 'slaveUnit') {
				this.reqData.slaveUnit = curPage.data.datas.name
			}
		}
	},
	onLoad(options) {
		this.reqData.name = options.name
		this.reqData.type = options.type
		this.reqData.masterUnit = options.masterUnit
		this.reqData.slaveUnit = options.slaveUnit
		this.reqData.multiple = options.multiple
		this.reqData.remarks = options.remarks
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSubmit() {
			uni.navigateBack()
		}
	},
	watch:{
		reqData: {
			handler(val) {
				if (val.name && val.type && val.unit) {
					this.disableSubmit = false
				} else {
					this.disableSubmit = true
				}
			},
			deep: true
		}
	}
}
